import { createFeatureSelector, createSelector } from '@ngrx/store';
import { FEATURE_STORE_SELECTOR } from '../constants/initital-state';
import { QuickTransferState as State } from '../models/state.model';

const child = createFeatureSelector<State>(FEATURE_STORE_SELECTOR);

export const selectFromAccount = createSelector(child, (state: State) => state['fromAccount']);
export const selectDebitAccounts = createSelector(child, (state: State) => state['debitAccounts']);
export const selectCreditAccounts = createSelector(child, (state: State) => state['creditAccounts']);
export const selectExtAccounts = createSelector(child, (state: State) => state['extAccounts']);
export const selectContacts = createSelector(child, (state: State) => state['contacts']);
export const selectAccountsCallState = createSelector(child, (state: State) => state['accountsCallState']);
export const selectContactsCallState = createSelector(child, (state: State) => state['contactsCallState']);
export const selectExtAccountsCallState = createSelector(child, (state: State) => state['extAccountsCallState']);
export const selectTransferOrderCallState = createSelector(child, (state: State) => state['transferOrderCallState']);
export const selectViewState = createSelector(child, (state: State) => state['viewState']);
export const selectPayeeAccount = createSelector(child, (state: State) => state['payeeTo']);
export const selectAmount = createSelector(child, (state: State) => state['amount']);
export const selectIsModalOpen = createSelector(child, (state: State) => state['isModalOpen']);
export const selectTouchedFields = createSelector(child, (state: State) => state['touchedFields']);
export const selectFocusElement = createSelector(child, (state: State) => state['focusElement']);
